// src/components/PermissionsDialog.tsx
import { useState } from "react";
import { Search, X, UserPlus, Shield } from "lucide-react";
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
} from "./ui/dialog";
import { Button } from "./ui/button";
import { Input } from "./ui/input";
import { Label } from "./ui/label";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "./ui/select";
import { Avatar, AvatarFallback } from "./ui/avatar";
import { Badge } from "./ui/badge";
import { Document } from "./DocumentsTable";

interface PermissionsDialogProps {
  open: boolean;
  onOpenChange: (open: boolean) => void;
  document: Document;
  // NOTE: S dng vai tr ngi dng hin ti  kim tra quyn
  currentUserRole?: "viewer" | "owner" | "admin" | "manager";
}

interface UserPermission {
  id: string;
  name: string;
  email: string;
  // UPDATED: Removed 'editor' role.
  role: "viewer" | "owner" | "admin" | "manager"; 
  department: string;
}

// Gi nh vai tr ngi dng hin ti l 'admin'  minh ha logic
const FAKE_CURRENT_USER_ROLE: UserPermission["role"] = "admin"; 

export function PermissionsDialog({ open, onOpenChange, document, currentUserRole = FAKE_CURRENT_USER_ROLE }: PermissionsDialogProps) {
  const [searchQuery, setSearchQuery] = useState("");
  const [permissions, setPermissions] = useState<UserPermission[]>([
    {
      id: "1",
      name: "Nguyn Vn A",
      email: "nguyenvana@company.com",
      role: "owner",
      department: "Cng ngh"
    },
    {
      id: "admin-404", 
      name: "Trn Vn Qun Tr",
      email: "admin@company.com",
      role: "admin",
      department: "Qun l"
    },
    {
      id: "manager-500", 
      name: "L Vn Qun L",
      email: "manager@company.com",
      role: "manager", // Vai tr Manager
      department: "iu hnh"
    },
    {
      id: "2",
      name: "Trn Th B",
      email: "tranthib@company.com",
      role: "viewer", // UPDATED: Changed from 'editor' to 'viewer'
      department: "Marketing"
    },
    {
      id: "3",
      name: "L Vn C",
      email: "levanc@company.com",
      role: "viewer",
      department: "Nhn s"
    }
  ]);

  const availableUsers = [
    { id: "4", name: "Phm Th D", email: "phamthid@company.com", department: "Ti chnh" },
    { id: "5", name: "Hong Vn E", email: "hoangvane@company.com", department: "Php l" },
  ];

  const handleRoleChange = (userId: string, newRole: string) => {
    // Ch Owner v Admin mi c thng cp ln Manager
    const canPromoteDemote = currentUserRole === "owner" || currentUserRole === "admin";
    const currentRole = permissions.find(p => p.id === userId)?.role;

    // Logic hn ch: Manager ch c th phn quyn gia Viewer (User)
    const isCurrentUserManager = currentUserRole === 'manager';
    const isTargetUserManagerOrHigher = currentRole === 'manager' || currentRole === 'admin' || currentRole === 'owner';
    
    // Nu ngi dng hin ti l Manager v ang c gng thay i quyn ca Manager/Admin/Owner, ngn chn
    if (isCurrentUserManager && isTargetUserManagerOrHigher) {
        console.warn("Permission denied: Manager khng th thay i vai tr ca Manager/Admin/Owner.");
        return;
    }
    
    // Gi logic thng cp/ging cp Admin/Owner
    if (newRole === "manager") { 
      if (!canPromoteDemote) {
         console.warn("Permission denied: Ch Ch s hu hoc Admin mi c th thng cp.");
         return;
      }
    }

    // Ch Owner v Admin mi c ging cp Manager (xung viewer)
    if (currentRole === "manager" && newRole !== "manager") {
       if (!canPromoteDemote) {
           console.warn("Permission denied: Ch Ch s hu hoc Admin mi c th ging cp Manager.");
           return;
       }
    }

    setPermissions(permissions.map(p => 
      p.id === userId ? { ...p, role: newRole as any } : p
    ));
  };

  const handleRemoveUser = (userId: string) => {
    const canPromoteDemote = currentUserRole === "owner" || currentUserRole === "admin";
    const targetUser = permissions.find(p => p.id === userId);
    
    // Ngn Manager b xa bi ngi dng cp thp hn
    if (targetUser?.role === "manager" && !canPromoteDemote) {
        console.warn("Permission denied: Ch Ch s hu hoc Admin mi c th xa Manager.");
        return;
    }

    // Manager khng c xa Admin/Owner
    if (currentUserRole === 'manager' && (targetUser?.role === 'owner' || targetUser?.role === 'admin')) {
         console.warn("Permission denied: Manager khng th xa Admin/Owner.");
         return;
    }
    
    setPermissions(permissions.filter(p => p.id !== userId));
  };
  
  const handleAddUser = (user: typeof availableUsers[0]) => {
    setPermissions([...permissions, { ...user, role: "viewer" }]);
  };

  const getRoleBadge = (role: UserPermission["role"]) => {
    switch (role) {
      case "owner":
        return <Badge>Ch s hu</Badge>;
      case "admin":
        return <Badge variant="destructive">Admin</Badge>;
      case "manager":
        return <Badge variant="secondary" className="bg-yellow-600 hover:bg-yellow-600/80 text-white">Qun l</Badge>; 
      case "viewer":
        return <Badge variant="outline">Ngi dng</Badge>; 
      default:
        return <Badge>{role}</Badge>;
    }
  };
  
  // Hm tr gip  xc nh vn bn hin th trong SelectTrigger
  const getSelectTriggerValue = (role: UserPermission["role"]) => {
    switch (role) {
      case "admin":
        return "Admin";
      case "owner":
        return "Ch s hu";
      case "manager":
        return "Manager";
      case "viewer":
        return "Xem"; 
      default:
        return "";
    }
  };

  const getInitials = (name: string) => {
    return name
      .split(" ")
      .map(n => n[0])
      .join("")
      .toUpperCase()
      .slice(0, 2);
  };

  const filteredUsers = availableUsers.filter(user =>
    !permissions.some(p => p.id === user.id) &&
    (user.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
     user.email.toLowerCase().includes(searchQuery.toLowerCase()))
  );


  return (
    <Dialog open={open} onOpenChange={onOpenChange}>
      <DialogContent className="sm:max-w-[700px]">
        <DialogHeader>
          <DialogTitle>Qun l quyn truy cp</DialogTitle>
          <DialogDescription>
            Qun l ngi dng c quyn truy cp vo "{document.name}"
          </DialogDescription>
        </DialogHeader>

        <div className="space-y-6 py-4">
          
          {/* Add User Search */}
          <div className="space-y-2">
            <Label>Thm ngi dng</Label>
            <div className="relative">
              <Search className="absolute left-3 top-1/2 h-4 w-4 -translate-y-1/2 text-muted-foreground" />
              <Input
                placeholder="Tm kim theo tn hoc email..."
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
                className="pl-9"
              />
            </div>
            {searchQuery && filteredUsers.length > 0 && (
              <div className="mt-2 max-h-[150px] overflow-y-auto rounded-lg border bg-background">
                {filteredUsers.map(user => (
                  <div
                    key={user.id}
                    className="flex items-center justify-between p-3 hover:bg-accent cursor-pointer"
                    onClick={() => {
                      handleAddUser(user);
                      setSearchQuery("");
                    }}
                  >
                    <div className="flex items-center gap-3">
                      <Avatar className="h-8 w-8">
                        <AvatarFallback>{getInitials(user.name)}</AvatarFallback>
                      </Avatar>
                      <div>
                        <div>{user.name}</div>
                        <div className="text-muted-foreground">{user.email}</div>
                      </div>
                    </div>
                    <UserPlus className="h-4 w-4" />
                  </div>
                ))}
              </div>
            )}
          </div>

          {/* Current Permissions */}
          <div className="space-y-2">
            <Label>Ngi dng c quyn truy cp ({permissions.length})</Label>
            <div className="space-y-2 max-h-[300px] overflow-y-auto">
              {permissions.map(user => {
                const isCurrentUserOwner = currentUserRole === "owner";
                const canPromoteDemote = currentUserRole === "owner" || currentUserRole === "admin";
                const isCurrentUserManager = currentUserRole === 'manager';
                
                const isOwner = user.role === "owner";
                const isAdmin = user.role === "admin";
                const isManager = user.role === "manager";
                const isUser = user.role === "viewer";
                
                // Target roles
                const isTargetTopTier = isOwner || isAdmin;
                const isTargetMidTierOrHigher = isTargetTopTier || isManager;
                
                // Visibility Logic
                // HIDE controls if target is Admin/Owner (v quyn ca h khng nn b thay i qua UI ny)
                const shouldHideControls = isTargetTopTier;
                
                // n dropdown v nt xa nu ngi dng hin ti l Manager v ang xem Manager khc
                const shouldHideControlsForManager = isCurrentUserManager && isManager;

                // Nt xa:
                const canRemoveTargetByAdminOwner = canPromoteDemote && !isOwner;
                const canRemoveTargetByManager = isCurrentUserManager && isUser;
                const canRemove = isCurrentUserOwner || canRemoveTargetByAdminOwner || canRemoveTargetByManager;


                const roleDisplay = getRoleBadge(user.role);

                // Hin th dropdown/controls nu target KHNG PHI Owner/Admin V (target khng phi Manager HOC current user l Admin/Owner)
                const shouldShowDropdownAndControls = !isTargetTopTier && !(isCurrentUserManager && isManager);


                return (
                  <div
                    key={user.id}
                    className="flex items-center justify-between rounded-lg border p-3"
                  >
                    <div className="flex items-center gap-3">
                      <Avatar>
                        <AvatarFallback>{getInitials(user.name)}</AvatarFallback>
                      </Avatar>
                      <div>
                        <div className="flex items-center gap-2">
                          <span>{user.name}</span>
                          {roleDisplay}
                        </div>
                        <div className="text-muted-foreground">
                          {user.email}  {user.department}
                        </div>
                      </div>
                    </div>
                    <div className="flex items-center gap-2">
                      
                      {/* Hin th DROPDOWN V NT XA */}
                      {shouldShowDropdownAndControls ? ( 
                        <>
                          <Select
                            value={user.role}
                            onValueChange={(value) => handleRoleChange(user.id, value)}
                            // Nu user l Manager, ch Owner/Admin mi c thay i vai tr ca h
                            disabled={isManager && !canPromoteDemote} 
                          >
                            <SelectTrigger className="w-[120px]"> {/* Gim chiu rng  trng ging User hn */}
                              {/* Hin th gi tr hin ti */}
                              <SelectValue placeholder={getSelectTriggerValue(user.role)} />
                            </SelectTrigger>
                            <SelectContent>
                              
                              {/* Logic  c sa: */}
                              {/* Cho php chn Manager nu: (Mc tiu l Viewer V c quyn thng cp) HOC Mc tiu  l Manager */}
                              {(isUser && canPromoteDemote) || isManager ? (
                                <SelectItem
                                    value="manager"
                                    // V hiu ha nt nu ang thng cp Viewer m khng c quyn
                                    disabled={isUser && !canPromoteDemote} 
                                >
                                    Manager
                                </SelectItem>
                              ) : null}

                              {/* Lun hin th Xem, nhng v hiu ha nu mc tiu l Manager v khng c quyn ging cp (ch Owner/Admin c quyn) */}
                              <SelectItem 
                                  value="viewer"
                                  disabled={isManager && !canPromoteDemote} 
                              >
                                  Xem
                              </SelectItem>

                            </SelectContent>
                          </Select>
                          
                          {/* Nt xa/hy quyn */}
                          <Button
                            variant="ghost"
                            size="icon"
                            onClick={() => handleRemoveUser(user.id)}
                            disabled={!canRemove} 
                          >
                            <X className="h-4 w-4" />
                          </Button>
                        </>
                      ) : (
                        // Trng hp HIDE: Ch hin th nt xa/hy quyn nu c quyn xa ngi dng cp cao
                        isTargetMidTierOrHigher && canPromoteDemote && (
                          <Button
                            variant="ghost"
                            size="icon"
                            onClick={() => handleRemoveUser(user.id)}
                            // Ch Owner/Admin mi c xa Admin/Owner/Manager khc
                            disabled={!canRemoveTargetByAdminOwner} 
                          >
                            <X className="h-4 w-4" />
                          </Button>
                      )
                      )}
                    </div>
                  </div>
                );
              })}
            </div>
          </div>
        </div>

        <DialogFooter>
          <Button variant="outline" onClick={() => onOpenChange(false)}>
            Hy
          </Button>
          <Button onClick={() => onOpenChange(false)}>
            Lu thay i
          </Button>
        </DialogFooter>
      </DialogContent>
    </Dialog>
  );
}