// src/lib/utils.ts
const API_BASE_URL = process.env.NEXT_PUBLIC_API_BASE_URL || 'http://localhost:4000';

interface ApiRequestOptions extends RequestInit {
  token?: string | null;
  isMultipart?: boolean;
  body?: any;
}

/**
 * Chun ha headers thnh Record<string, string>
 */
function normalizeHeaders(headers?: HeadersInit): Record<string, string> {
  if (!headers) return {};
  if (headers instanceof Headers) {
    const result: Record<string, string> = {};
    headers.forEach((value, key) => (result[key] = value));
    return result;
  }
  if (Array.isArray(headers)) {
    const result: Record<string, string> = {};
    headers.forEach(([key, value]) => (result[key] = value));
    return result;
  }
  return headers; //  l Record<string, string>
}

/**
 * Hm tin ch  gi API backend NestJS.
 * T ng thm JWT v x l JSON/li.
 */
export async function fetchApi<T>(endpoint: string, options: ApiRequestOptions = {}): Promise<T> {
  const { token, isMultipart, headers, body, ...rest } = options;

  const defaultHeaders: Record<string, string> = {
    'Accept': 'application/json',
    ...normalizeHeaders(headers),
  };

  if (!isMultipart) {
    defaultHeaders['Content-Type'] = 'application/json';
  }

  const finalHeaders: Record<string, string> = {
    ...defaultHeaders,
    ...(token ? { 'Authorization': `Bearer ${token}` } : {}),
  };

  // Ch stringify nu khng phi FormData
  const fetchBody: BodyInit | undefined = body
    ? !isMultipart && !(body instanceof FormData)
      ? JSON.stringify(body)
      : body
    : undefined;

  const response = await fetch(`${API_BASE_URL}${endpoint}`, {
    ...rest,
    headers: finalHeaders,
    body: fetchBody,
  });

  const responseText = await response.text();

  if (!response.ok) {
    let errorData = { message: 'An unknown error occurred' };
    try {
      errorData = JSON.parse(responseText);
    } catch (e) {
      errorData.message = responseText || response.statusText;
    }
    throw new Error(errorData.message);
  }

  try {
    return responseText ? (JSON.parse(responseText) as T) : ({} as T);
  } catch (e) {
    return {} as T;
  }
}

/**
 * Chun ha ngy thng t nh dng dd/mm/yyyy (ting Vit) sang i tng Date chun.
 * Gip vic sp xp theo thi gian c chnh xc.
 */
export const parseDate = (dateString: string): Date => {
  const parts = dateString.split('/');
  if (parts.length !== 3) {
      // Fallback cho trng hp nh dng khng ng, tr v ngy khng hp l
      return new Date(NaN);
  }
  // parts[0] = ngy (dd), parts[1] = thng (mm), parts[2] = nm (yyyy)
  // Lu : Thng trong JS l 0-indexed (0=thng 1, 11=thng 12), nn phi tr 1.
  return new Date(parseInt(parts[2]), parseInt(parts[1]) - 1, parseInt(parts[0]));
}

/**
 * Hm chuyn i kch thc file thnh s bytes  so snh.
 * H tr cc n v KB, MB, GB.
 */
export const sizeToNumber = (size: string): number => {
  if (!size) return 0;
  const parts = size.trim().split(/\s+/);
  const rawValue = parts[0]?.replace(',', '.');
  const unit = (parts[1] || "").toUpperCase();
  const value = parseFloat(rawValue) || 0;

  switch (unit) {
    case "GB":
      return value * 1024 * 1024 * 1024;
    case "MB":
    case "M":
      return value * 1024 * 1024;
    case "KB":
    case "K":
      return value * 1024;
    default:
      // Gi s bytes nu khng c unit
      return value;
  }
};

